<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoleDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="roleList" border stripe>
        <!--  type="expand"  slot  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for   -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(scope.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeRoleById(scope.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRoleDialogVisible"
      width="40%"
      @close="addRoleDialogClosed"
    >
      <el-form
        ref="addRoleFormRef"
        :model="addRoleForm"
        :rules="addRoleFormRules"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="addRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addRole"> </el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editRoleDialogVisible"
      width="40%"
      @close="addRoleDialogClosed"
    >
      <el-form
        :model="editRoleForm"
        ref="editRoleFormRef"
        :rules="editRoleFormRules"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoles"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @click="setRightDialogClosed"
    >
      <!--  -->
      <el-tree
        :data="rightList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="setRightDialogVisible = false"> </el-button>
          <el-button type="primary" @click="allotRights"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      roleList: [],
      // 
      setRightDialogVisible: false,
      // 
      rightList: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      roleId: 0,
      // 
      addRoleDialogVisible: false,
      // 
      addRoleForm: {},
      // 
      addRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editRoleForm: {},
      // 
      editRoleDialogVisible: false,
      // 
      editRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.roleList = res.data

      console.log(this.roleList)
    },
    // Id
    async removeRightById (role, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      role.children = res.data
    },
    // 
    async showSetRightDialog (role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.rightList = res.data

      // id
      this.getLeafkeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },
    // iddefKeys
    getLeafkeys (node, arr) {
      // nodechildren
      if (!node.children) {
        return arr.push(node.id)
      }
      // 
      node.children.forEach(element => {
        this.getLeafkeys(element, arr)
      })
    },
    // 
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 
    addRightDialogClosed () {
      this.$refs.addRoleFormRef.resetFields()
    },
    // 
    addRole () {
      this.$refs.addRoleFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('roles', this.addRoleForm)
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addRoleDialogVisible = false
        // 
        this.getRolesList()
      })
    },
    // 
    async showEditDialog (id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editRoleForm = res.data
      this.editRoleDialogVisible = true
    },
    editRoles () {
      this.$refs.editRoleFormRef.validate(async valid => {
        // console.log(valid)
        // 
        if (!valid) return
        const { data: res } = await this.$http.put(
          'roles/' + this.editRoleForm.roleId,
          {
            roleName: this.editRoleForm.roleName,
            roleDesc: this.editRoleForm.roleDesc
          }
        )
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        // 
        this.editRoleDialogVisible = false
        this.$message.success('')
        this.getRolesList()
      })
    },
    // 
    async removeRoleById (id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getRolesList()
    },
    // 
    async allotRights (roleId) {
      // Id
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // join() 
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('!')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style land="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>
