<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <!--  queryInfo.query ,getUserList -->
          <!-- clearable -->
          <!-- ,clear getUserList  -->
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userlist" border stripe>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
          <!--   -->
          <template slot-scope="scope">
            <!--  -->
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>

        <el-table-column label="" width="180px">
          <!-- scope -->
          <template slot-scope="scope">
            <!--  -->
            <!-- contenthoverplacement -->
            <!-- enterable  tooltip  -->
            <el-tooltip content="" placement="top" :enterable="false">
              <!-- size  -->
              <el-button
                type="primary"
                icon="el-icon-share"
                size="mini"
                @click="showEditDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserById(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <!-- size-changecurrent-change
      page-sizes -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[2, 10, 20, 50]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <!-- addDialogVisible  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRoleDialogVisible"
      width="50%"
    >
      <div>
        <p>{{ userInfo.username }}</p>
        <p>{{ userInfo.role_name }}</p>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="setRoleDialogVisible = false"
            > </el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    // 
    const checkEmail = (rule, value, callback) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if (regEmail.test(value)) {
        return callback()
      }

      callback(new Error(''))
    }

    // 
    const checkMobile = (rule, value, callback) => {
      const regMobile = /^(0|86|17591)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return callback()
      }

      callback(new Error(''))
    }

    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      // 
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        email: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkEmail,
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ]
      },
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: []
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.userlist = res.data.users
      this.total = res.data.total
      console.log(res)
    },
    // pagesize
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.pagesize = newSize
      // 
      this.getUserList()
    },
    // 
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.pagenum = newPage
      // 
      this.getUserList()
    },
    //  switch 
    async userStateChanged (userInfo) {
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    addDialogClosed () {
      // 
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser () {
      this.$refs.addFormRef.validate(async valid => {
        // 
        if (!valid) return
        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          this.$message.error('')
        }

        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog (id) {
      // console.log(id)
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // id
    async removeUserById (id) {
      console.log(id)
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      // 
      this.getUserList()
    },
    async setRole (userInfo) {
      this.userInfo = userInfo

      const { data: res } = await this.$http.get('role')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.rolesList = res.data
      this.setRoleDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped></style>
